/* ==========================================================================
   Product List - Carousel shortcode responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
    
    .qode-plc-holder {

        &.qode-simple-type {
        
            .qode-plc-image-outer,
            .qode-plc-text-wrapper {
                display: inline-block;
                width: 100%;
                padding: 0;
            }

            .qode-plc-text-wrapper {
                margin: 30px 0 40px;
            }
        }

        .qode-owl-slider .owl-nav{
            .owl-prev {
                @include qodeTransform(translateX(0%))
            }
            .owl-next{
                @include qodeTransform(translateX(0%))
            }
        }
    }
}
/* ==========================================================================
   Product List - Carousel shortcode responsive styles - end
   ========================================================================== */